@import url(https://fonts.googleapis.com/css?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css?family=Patua+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Squada+One&display=swap);
html {
  height: 100%;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: inherit;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100px;
}
#logo {
  padding-left: 15px;
  font-family: "Lobster", sans-serif;
}
#logo a {
  color: #ee6123;
}
#login-logout {
  display: flex;
  justify-content: space-between;
  width: 10%;
  padding-right: 10px;
}
#login:hover {
  color: #2a5bd7;
}
.header-text {
  padding: 5px;
}
.sign-text {
  border: 2px solid #ee6123;
  box-sizing: border-box;
  -webkit-transition: ease 0.5s;
  transition: ease 0.5s;
  text-decoration: none !important;
}
.sign-text:hover {
  background: #ee6123;
  color: white;
}
a {
  text-decoration: none !important;
}
#dashboard-tab {
  height: 50px;
  width: 300px;
  padding: 5px 0px;
  margin-right: 40px;
}

#dashboard-contents {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
}
#dashboard-one {
  display: flex;
  flex-direction: row;
  height: 50px;
  align-items: center;
}

#dashboard-img {
  -webkit-transform: scale(0.7);
          transform: scale(0.7);
  position: relative;
  bottom: 5px;
  right: 5px;
  width: 50px;
  height: 50px;
}

#dashboard-two {
  display: flex;
}
.text-cred {
  margin: 5px 10px;
}
/* media query mobile only*/
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  #login-logout {
    width: 30%;
    padding-right: 0px;
    margin-right: 5px;
  }
  .header-text {
    padding: 0px;
  }
  .sign-text:hover {
    background: transparent;
    color: black;
  }
  .sign-text {
    border: none;
  }
  #dashboard-tab {
    width: 0;
    margin-right: 0px;
    display: contents;
  }
  #dashboard-contents {
    flex-direction: column;
  }
  #dashboard-one {
    justify-content: center;
  }
}

#banner-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 550px;
}
#banner-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
}
#banner-head {
  font-size: 50px;
}
#banner-para {
  color: grey;
  font: 25px;
}

#banner-image {
  background: url(/shortify/static/media/intro.b160bcdd.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
}

/* media query mobile only*/
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  #banner-container {
    flex-wrap: wrap-reverse;
    height: 450px;
  }
  #banner-text {
    width: 100%;
    justify-content: center;
  }
  #banner-head {
    font-size: 40px;
  }
  #banner-para {
    margin-top: -10px;
    height: auto;
    height: initial;
  }
  #banner-image {
    height: 240px;
  }
}

#link-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: lightcoral;
  height: 120px;
}
#link-tab {
  width: 90%;
}
#message {
  text-align: center;
  padding-top: 10px;
  font-weight: bold;
}

#feature-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 90%;
  margin: 0 auto;
  height: 650px;
}
.feature-list {
  display: flex;
  justify-content: space-between;
  margin: 50px 0px;
}
.feature-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
}
.feature-image {
  width: 128px;
  height: 128px;
}
.feature-text,
.feature-para {
  text-align: center;
  margin: 5px 0px;
}
.feature-para {
  font-size: 17px;
}

/* media query mobile only*/
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  #feature-container {
    height: 1700px;
    justify-content: center;
  }
  .feature-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0px;
  }
  .feature-box {
    width: 70%;
    margin: 15px 0px;
  }
}

#footer-container {
  display: flex;
  width: 100%;
  background-color: #333333;
}
#footer-logo {
  color: #ee6123;
  padding-left: 15px;
  font-family: "Lobster", sans-serif;
}
#footer-text {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
#footer-author {
  height: 50px;
  margin-right: 15px;
  padding-top: 20px;
  color: white;
}

/* media query mobile only*/
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  #footer-container {
    min-height: 11rem;
  }
  #footer-text {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  #footer-logo {
    text-align: center;
    padding: 0px;
  }
  #footer-author {
    text-align: center;
    margin: 0px;
    padding: 0px;
  }
}

#login-container {
  display: flex;
  width: 100%;
  max-height: 100vh;
  background: #fff;
}
#login-image {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
#login-image img {
  width: 80%;
}
#login-form {
  width: 50%;
}
form {
  width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  height: 555px;
  margin-top: -50px;
}
.input-form-elements {
  margin: 10px 0px;
}
form input {
  outline: none;
  background: transparent;
  border: none;
  border-bottom: 2px solid black;
}
form button {
  outline: none;
  background: transparent;
  border: 1px solid #ee6123;
}
form button:hover {
  background: #ee6123;
  color: white;
}
#login-text {
  text-align: center;
  position: relative;
  top: 30px;
  font-family: monospace;
  color: #ee6123;
  font-weight: 900;
  font-size: 40px;
}
/* media query mobile only*/
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  #login-container {
    flex-direction: column;
    max-height: -webkit-fit-content;
    max-height: -moz-fit-content;
    max-height: fit-content;
  }
  #login-image,
  #login-form {
    width: 100%;
  }
  #login-form {
    min-height: 320px;
  }
  form {
    height: inherit;
    margin-top: 0px;
  }
  #login-text {
    top: -2px;
  }
}

#register-container {
  display: flex;
  width: 100%;
  max-height: 100vh;
  background: #f8fafb;
}
#register-image {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
#register-image img {
  width: 80%;
}
#register-form {
  width: 50%;
}
form {
  width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  height: 555px;
  margin-top: -50px;
}
.form-elements {
  margin: 10px 0px;
}
form input {
  outline: none;
  background: transparent;
  border: none;
  border-bottom: 2px solid black;
}
form button {
  outline: none;
  background: transparent;
  border: 1px solid #ee6123;
}
form button:hover {
  background: #ee6123;
  color: white;
}
#register-text {
  text-align: center;
  position: relative;
  top: 30px;
  font-family: monospace;
  color: #ee6123;
  font-weight: 900;
  font-size: 40px;
}
#success-text {
  color: red;
  text-align: center;
}
/* media query mobile only*/
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  #register-container {
    flex-direction: column;
    max-height: -webkit-fit-content;
    max-height: -moz-fit-content;
    max-height: fit-content;
  }
  #register-image,
  #register-form {
    width: 100%;
  }
  #register-form {
    min-height: 400px;
  }
  form {
    height: inherit;
    margin-top: 0px;
  }
  #register-text {
    top: -25px;
  }
}

#page-not-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 555px;
}
.error {
  font-family: "Press Start 2P";
  font-size: 200px;
  margin-top: 30px;
  color: #ee6123;
  opacity: 0.8;
}
.error-text {
  margin-top: 10px;
  font-size: 50px;
  font-weight: 900;
}
.error-text::before {
  content: "Oh no.....";
}
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  #page-not-container {
    min-height: 450px;
  }
  .error {
    font-size: 100px;
  }
  .error-text {
    font-size: 30px;
    display: flex;
    justify-content: center;
  }
  .error-text::before {
    content: "";
  }
  .not-mobile {
    margin: 30px;
  }
}

#analysis-container {
  display: flex;
  width: 35%;
  height: 555px;
}
#analysis-left {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: inherit;
  background: #f9f9fc;
}
.analysis-tab {
  display: flex;
  width: 60%;
  height: 120px;
  box-sizing: border-box;
  border-radius: 10px;
}
.tab1 {
  background: -webkit-gradient(linear, left top, right top, from(#fe9365), to(#feb798));
  background: linear-gradient(to right, #fe9365, #feb798);
}
.tab2 {
  background: -webkit-gradient(linear, left top, right top, from(#0ac282), to(#0df3a3));
  background: linear-gradient(to right, #0ac282, #0df3a3);
}
.tab3 {
  background: -webkit-gradient(linear, left top, right top, from(#fe5d70), to(#fe909d));
  background: linear-gradient(to right, #fe5d70, #fe909d);
}
#analysis-content {
  height: inherit;
  width: 100%;
  display: flex;
}
.analysis-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 60%;
  height: inherit;
  font-family: "Patua One";
  color: white;
  font-size: 23px;
  word-break: break-all;
}
.analysis-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40%;
  height: inherit;
}
img {
  width: 40%;
}
/* media query mobile only*/
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  #analysis-container {
    width: 100%;
    height: 140px;
  }
  #analysis-left {
    width: 33.3%;
    display: contents;
  }

  .analysis-text {
    justify-content: unset;
    text-align: center;
  }

  .analysis-tab {
    display: block;
    width: 100%;
    border-radius: 0px;
  }
  p {
    word-break: break-word;
  }
  .mobile-res {
    font-size: 17px;
  }
}

#user-link-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  box-sizing: border-box;
  overflow: scroll;
  max-height: 435px;
}
#user-link-tab {
  border-bottom: 1px solid gray;
  width: 96%;
  height: 120px;
  margin: 0 auto;
}
#user-link-top {
  display: flex;
  height: 20px;
  padding-top: 2px;
}
.cred {
  padding-left: 8px;
  font-size: 12px;
  font-family: monospace;
  color: #24242b;
}
#user-link-middle {
  display: flex;
  align-items: center;
  height: 60px;
  padding-left: 8px;
  font-family: "Squada One";
  font-size: 15px;
}
#user-link-bottom {
  display: flex;
  padding-left: 8px;
}
.user-link-btn {
  outline: none;
  border: 0;
  border: 1px solid #ee6123;
  font-size: 10px;
  height: 20px;
  margin: 0px 8px;
}
.user-link-btn:hover {
  background-color: #ee6123;
}
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  #user-link-container {
    width: 100%;
  }
  #user-link-top {
    padding: 0;
  }
  #user-link-tab {
    height: 9rem;
  }
  #user-link-container {
    min-height: 309px;
  }
}

#dashboard-container {
  display: flex;
  width: 100%;
}
#link-tab-section {
  width: 65%;
}
/* media query mobile only*/
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  #dashboard-container {
    display: block;
    width: 100%;
  }
  #link-tab-section {
    width: 100%;
  }
}

#dashboard-link-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: lightcoral;
  height: 120px;
}
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  #dashboard-link-container {
    margin-top: -20px;
  }
}

